(function () {
    if (window.avast_engagement) return;
    window.avast_engagement = true;

    let defaultStyle = {
        display: "block",
        width: "100%",
        height: "100%",
        margin: "0",
        padding: "0",
        position: "fixed",
        top: "0",
        right: "0",
        border: "0",
        "z-index": "2147483647"
    };
    let iframe = document.createElement("iframe");
    iframe.setAttribute("scrolling", false);
    iframe.setAttribute("seamless", true);
    Object.assign(iframe.style, defaultStyle);
    chrome.runtime.onMessage.addListener((message, ...arg) => {
        console.log ('inject message', message, arg)
        if (message.action == 'close') {
            document.documentElement.removeChild(iframe);
        } else if (message.action == 'open') {
            iframe.src = message.url;
            Object.assign(iframe.style, {
                width: message.width + 'px',
                height: message.height + 'px'
            });
            document.documentElement.appendChild(iframe);
        } else if (message.action == 'setStyle') {
            Object.assign(iframe.style, message.style);
        }
    });
})();